// @flow

import * as React from 'react';
import { graphql, createFragmentContainer } from 'react-relay';
import {
  BaggageCabin,
  BaggageChecked,
} from '@kiwicom/orbit-components/lib/icons';

import type { BaggageDescription as BaggageDescriptionProps } from './__generated__/BaggageDescription.graphql';

type Props = {|
  type: 'Cabin' | 'Checked',
  data: BaggageDescriptionProps,
|};

function renderIcon(type: string) {
  switch (type) {
    case 'Cabin':
      return <BaggageCabin size="medium" customColor="#bac7d5" />;
    case 'Checked':
      return <BaggageChecked size="medium" customColor="#bac7d5" />;
  }
  return null;
}

export const BaggageDescription = ({
  type,
  data: { height, weight, width, length },
}: Props) => {
  return (
    <React.Fragment>
      <hr className="separationLine" />
      <div className="baggageRow">
        {renderIcon(type)}
        <p className="baggageWeight">
          {type} baggage {weight} kg
        </p>
        <div className="baggageSize">
          <p>
            {height} x {width} x {length} cm
          </p>
        </div>
      </div>
      <style jsx>
        {`
          div.baggageRow {
            padding: 15px 24px 15px 24px;
          }
          div.baggageSize {
            display: inline-block;
            float: right;
          }
          div.baggageSize p {
            font-size: 12px;
            line-height: 1.4;
            color: #46515e;
          }
          div.baggageNumber {
            display: inline-block;
            margin-right: 8px;
          }
          div.baggageNumber p {
            font-size: 14px;
            font-weight: bold;
            line-height: 1.4;
            color: #46515e;
            display: inline-block;
          }
          p.baggageWeight {
            margin-left: 5px;
            font-size: 14px;
            font-weight: bold;
            line-height: 1.4;
            color: #46515e;
            display: inline-block;
          }
          hr.separationLine {
            height: 1px;
            background-color: #e8edf1;
            border: none;
          }
        `}
      </style>
    </React.Fragment>
  );
};

export default createFragmentContainer(
  BaggageDescription,
  graphql`
    fragment BaggageDescription on Baggage {
      height
      weight
      width
      length
    }
  `,
);
